}
@media only screen and (min-width : 768px) {
  .table-scrollable tbody tr {
    width: 15em;
  }
}
.table-scrollable tbody tr + tr {
  border-left: #a4cded 1px solid;
}
.table-scrollable tbody td {
  display: block;
  width: 100%;
  font-size: 12px;
  line-height: 1.3;
  height: 7rem;
