  color: #000;
  padding: 10px 15px;
  display: block;
  width: 100%;
}
.accordion .card-body {
  padding: 1rem;
}
.accordion + .accordion {
  margin-top: -1rem;
}
.accordion + .accordion .card-header {
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}
.accordion .accordion .card-title {
