/**
* Some times the defualt margin needs to be removed.  Adding these class will selectivly remove the margin of the DOM item it is placed on. 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin No Margin
* @example
*  class="m-0"              will remove the margin on all sides
*  class="no-margin"        Alias for m-0
*  class="m-l-0"            will remove the margin on the left
*  class="m-r-0"            will remove the margin on the right
*  class="m-t-0"            will remove the margin on the top
*  class="m-b-0"            will remove the margin on the bottom 
* 
*/
.m-0, .no-margin, .m-l-0 {
  margin-left: 0 !important;
}

.m-0, .no-margin, .m-r-0 {
  margin-right: 0 !important;
}

.m-0, .no-margin, .m-t-0 {
  margin-top: 0 !important;
}

.m-0, .no-margin, .m-b-0 {
  margin-bottom: 0 !important;
}

/**
* Some times the defualt padding needs to be removed.  Adding these class will selectivly remove the padding of the DOM item it is placed on. 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin No Padding
* @example
*  class="p-0"              will remove the padding on all sides
*  class="no-padding"       Alias for p-0
*  class="p-l-0"            will remove the padding on the left
*  class="p-r-0"            will remove the padding on the right
