}

.p-0, .no-padding, .p-r-0, .no-gutter > .sf_cols > [class*=col-], .no-gutter > [class*=col-] {
  padding-right: 0 !important;
}

.p-0, .no-padding, .p-t-0 {
  padding-top: 0 !important;
}

.p-0, .no-padding, .p-b-0 {
  padding-bottom: 0 !important;
}

/**
* No Gutter removes the gutter/padding of column directly contained.. 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin No Padding
* @example
*  class="p-0"              will remove the padding on all sides
*  class="no-padding"       Alias for p-0
*  class="p-l-0"            will remove the padding on the left
*  class="p-r-0"            will remove the padding on the right
*  class="p-t-0"            will remove the padding on the top
*  class="p-b-0"            will remove the padding on the bottom 
* 
*/
/**
* Expanded will change any object to 100% width with 100% max-width 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin Expanded
* @example
*  class="expanded"              wiidth="100%"; max-width="100%"
* 
*/
.expanded {
  width: 100% !important;
  max-width: 100% !important;
}

.input-group.expanded {
  width: 100% !important;
  max-width: 100% !important;
}
.input-group.expanded > * {
  width: 100% !important;
  max-width: 100% !important;
}

/**
* Apply a multi column css3 property to a container with the deonted break point.
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin Multi-column
* @example
*  class="multi-column-md-2"              2 column container that collapses back to 1 when screen is below medium
*  class="multi-column-lg-2"              2 column container that collapses back to 1 when screen is below large
* 
*/
@media only screen and (min-width : 768px) {
  .multi-column-md-2 {
    -moz-column-count: 2;
    -webkit-column-count: 2;
    column-count: 2;
    -moz-column-gap: 10px;
    -webkit-column-gap: 10px;
    column-gap: 10px;
  }
}

@media only screen and (min-width : 1024px) {
  .multi-column-lg-2 {
    -moz-column-count: 2;
    -webkit-column-count: 2;
    column-count: 2;
    -moz-column-gap: 10px;
    -webkit-column-gap: 10px;
    column-gap: 10px;
  }
}

/**
* Content padding often you will need to inject some spacing into the left and right of a container so the content and container do not collide. 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin Content Padding
* @example
*  class="content-padding"              will add a full gutter space on the left and right inside a container
*  class="content-padding half"         will add a 1/2 the  gutter space on the left and right inside a container
*  class="content-padding quarter"      will add a 1/4 gutter space on the left and right inside a container
* 
*/
.content-padding {
  padding-left: 18px;
  padding-right: 18px;
}
.content-padding.half {
  padding-left: 9px;
  padding-right: 9px;
}
.content-padding.quarter {
  padding-left: 4.5px;
  padding-right: 4.5px;
}

/**
* section padding often you will need to inject some spacing into the top and bottom of a container so the section and container do not collide. 
* ~~ Open the example to see the other class and how they will effect the design ~~
*
* @section Helpers > trumps
* @mixin Section Padding
* @example
*  class="section-padding"              will add a full line-height space on the top and bottom inside a container
*  class="section-padding half"         will add a 1/2 the line-height space on the top and bottom inside a container
*  class="section-padding quarter"      will add a 1/4 line-height space on the top and bottom inside a container
* 
*/
.section-padding {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}
.section-padding.half {
  padding-top: 0.75rem;
  padding-bottom: 0.75rem;
}
.section-padding.quarter {
  padding-top: 0.375rem;
  padding-bottom: 0.375rem;
}

